const weather = document.getElementById("weather");
const input = document.getElementById("input");
const buttonType = document.getElementById("buttonType");
const API_KEY = "f00881d88585865c3fb2a27151b436e6";
buttonType.addEventListener("click", (e) => {
  e.preventDefault();
  getWeather(input.value);
  input.value = "";
});

const getWeather = (city) => {
  fetch(
    `https://api.openweathermap.org/data/2.5/weather?q=${city}&appid=${API_KEY}`
  )
    .then((res) => res.json())
    .then((data) => weatherData(data));
};

const weatherData = (data) => {
  weather.innerHTML = "";
  weather.innerHTML += `<div class='weather_'>
<p>${data.name}</p>
<p>description${data.weather && data.weather.map((e) => e.description)}</p>
<p> main temp is : ${Math.floor(data.main.temp - 273.15)}&deg;</p>
<p> Feels like ${(data.main.feels_like - 273.15).toFixed(1)}&deg;</p>
<span><img className='img' src=${`http://openweathermap.org/img/w/${data.weather[0].icon}.png`}
alt='Icon depicting current weather.'/> </span>
</div>`;
};
